"use client"
import React, { useEffect, useState } from "react";
import Slideshow from "./ui/slideshow/Slideshow";
import { useBannerQuery } from "@/hooks/queries/useBannerQuery";
import useEffectAfterMount from "@/hooks/useEffectAfterMount";

const SpaceSlideshow = () => {
  const [index, setIndex] = useState<number>(0);
  const { data, isError, isLoading } = useBannerQuery();

  useEffect(()=>{
    function test() {
      setIndex(1)
    }
  }, [])

  useEffectAfterMount(()=>{
    console.log("Index changed")
  }, [index])

  return (
    <article className="w-full h-[740px]">
      <Slideshow index={index} setIndex={setIndex} autoplay transitionDuration={1} slideDuration={5} verticalMode>
        {data &&
          data.map((bannerData) => (
            <figure key={bannerData._id} className="w-full">
              <img className="w-full object-cover" src={`http://localhost:4444/images/banner/${bannerData.image}`} alt="Image for banner" />
            </figure>
          ))}
      </Slideshow>
    </article>
  );
};

export default SpaceSlideshow;
